import React from "react";
import SectionTitle from "../../components/Misc/SectionTitle";
import { useTheme } from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";
// yt video player
import YouTube from "react-youtube";
// styles
import { styled } from "@material-ui/core/styles";

const Wrapper = styled("div")(({ theme }) => ({
  width: "100vw",
  paddingBottom: 50,
  zIndex: 10,
  display: "flex",
  flexDirection: "column",
  justifyContent: "center",
  alignItems: "center",
  boxShadow: theme.shadows.sectionShadow
}));

// https://pacoup.com/2011/06/12/list-of-true-169-resolutions/
const Presentation = () => {
  const theme = useTheme();
  const matchesMobileMd = useMediaQuery(theme.breakpoints.down("md"));
  const matchesMobileSm = useMediaQuery(theme.breakpoints.down("sm"));
  const matchesMobileXs = useMediaQuery(theme.breakpoints.down("xs"));

  const videoStyles = matchesMobileXs
    ? { height: 216, width: 384 }
    : matchesMobileSm
    ? { height: 360, width: 640 }
    : matchesMobileMd
    ? { height: 504, width: 896 }
    : // desktop
      { height: 576, width: 1024 };

  return (
    <Wrapper id="presentation">
      <SectionTitle>Prezentare</SectionTitle>
      <YouTube videoId="lKhgGcjJpnc" opts={videoStyles} />
    </Wrapper>
  );
};

export default Presentation;
