import React from "react";
import ReadMore from "../../components/Misc/SubmitButton";
import Dividier from "../../components/Misc/Divider";
import { useTheme } from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import LandingTitle from "./components/LandingTitle";
import LandingDescription from "./components/LandingDescription";
// services
import { scrollToDiv } from "../../services";
// statiscs
import Background from "./assets/salon.jpg";
// styles
import { styled } from "@material-ui/core/styles";

const Wrapper = styled("div")(({ theme }) => ({
  width: "100vw",
  height: "110vh",
  color: "white",
  backgroundColor: theme.palette.green.main,
  boxShadow: theme.shadows.sectionShadow
}));

const Img = styled("div")(({ theme }) => ({
  height: "35vw",
  width: "35vw",
  position: "absolute",
  left: "50%",
  top: "20%",

  backgroundImage: `url(${Background})`,
  /* Center and scale the image nicely */
  backgroundPosition: "center",
  backgroundRepeat: "no-repeat",
  backgroundSize: "contain",
  [theme.breakpoints.down("md")]: {
    height: "40vmin",
    width: "40vmin",
    left: "35%",
    top: "10%"
  }
}));

const PresentationDiv = styled("div")(({ theme }) => ({
  width: "30vw",
  position: "absolute",
  left: "10%",
  top: "45%",
  zIndex: 10,
  display: "flex",
  flexDirection: "column",
  justifyContent: "center",
  alignItems: "center",
  [theme.breakpoints.down("md")]: {
    width: "90vw",
    left: "5%",
    top: "50%"
  }
}));

const Home = () => {
  const theme = useTheme();
  const matchesDesktop = useMediaQuery(theme.breakpoints.up("lg"));

  return (
    <Wrapper>
      <PresentationDiv>
        <LandingTitle>PlanInsta</LandingTitle>
        <Dividier />
        <LandingDescription>
          Înfrumusețare și relaxare la un click distanță
        </LandingDescription>
        <ReadMore
          size="large"
          onClick={() => scrollToDiv("presentation", !matchesDesktop)}
        />
      </PresentationDiv>
      <Img id="home" />
    </Wrapper>
  );
};

export default Home;
