import theme from "../theme";

export const scrollToDiv = (id, mobile = false) => {
  const elem = document.getElementById(id);
  const rect = elem.getBoundingClientRect();
  const navBarOffset = mobile
    ? theme.heights.navBarMobile
    : theme.heights.navBar;

  window.scrollTo({
    top: rect.top + window.scrollY - navBarOffset,
    behavior: "smooth"
  });
};

export const scrollToTop = () => {
  window.scrollTo({
    top: 0,
    behavior: "smooth"
  });
};
